.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hot_row1 {
  display: flex;
  justify-content: space-between;
  width: 90%;
  margin: auto;
  margin-top: 20px;
}

.hot_card1 {
  background: lightgray;
  width: 25%;
  border-radius: 10px;
  height: 120px;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  border: 1px solid #a2a2a2;
}

p.hot_p1 {
  color: #fff;
  font-size: 18px;
  margin: 5px;
  font-weight: 600;
  letter-spacing: 1px;
}


h3.project_name {
  font-size: 24px;
  margin-left: 20px;
  font-weight: 500;
  color: #000000;
}

.header {
  background: #fff;
  box-shadow: 1px 1px 10px #f2f2f2;
  height: 60px;
  display: flex;
  justify-content: space-between;
  border: 3px solid #f2f2f2;
  align-items: center;
}

p.hot_p2 {
  color: #121211;
  font-weight: 700;
  font-size: 30px;
  margin: 0;
}
.drop1 {
  cursor: pointer;
  /* width: 62px; */
  border: 1px solid #f2f2f2;
  box-shadow: 1px 1px 15px #d4c2c2;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-content: center;
  border-radius: 10px;
  align-items: center;

}
p.drop_data_p1 {
  padding: 2px 50px;
  font-size: 16px;
}

p.drop_p1 {
  padding: 2px 26px;
}
div.drop_down_project,
div.drop_down_data {
  background: #fff;
  position: absolute;
  top: 15%;
  box-shadow: -4px 13px 15px #d4c2c2;
  z-index: 10;
  padding: 5px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  font-size: 14px;
}

.drop_p1:hover {
  background-color: #f5f5f5;
  border-radius: 10px;
}

.ottLogo{
  height:"100px"
}